  .Nav {
    width:100vw;
    height: var(--nav-size);
    max-height:20vh;
    margin-top:2vh;

  /*  background-color: var(--bg);*/
    padding: 0 5px;
 /*   border-bottom: var(--border);*/

    position: fixed;

    display: flex;
    flex-direction: row; 
    justify-content: flex-end;
    align-items: center;
  }

  .Nav-Logo {
  /*  display: block;*/
    height:80px;
    width:80px;
  }
  
  .Nav-Logo:hover {
    transform: rotate(-10deg);
  }

  .Nav-Title {
  /*  display: block;*/
    margin-left:20px; 
    font-size: 40px;
    text-decoration: bold;
   /* text-shadow: 2px 2px 5px red;*/
    color: rgb(67, 29, 102);
  }

  .Nav-List {
    background-color: rgba(48, 46, 46, 0.7);

    margin-left: 15vw;
    margin-right: 5vw;
   /* padding: 0;*/
  /*  overflow: hidden;*/
  /*  background: rgba(242, 227, 245, 0.103);*/
    border: var(--border);
    border-radius: var(--border-radius);
    padding: 5px;

    display: flex;
    flex-direction: row; 
    justify-content: flex-end;
    align-items: center;
  }
 
  .Nav-Item {
  /*  background-color: rgba(94, 91, 92, 0.5);*/
  /*   display: block;*/

   /* width: 100%;
    height: 100%;*/

    width: calc(var(--nav-size) * 2.0);

   /* border: var(--border);*/
    border-radius: var(--border-radius);
    padding: 10px;

     color: rgb(0, 0, 48);
     font-weight: bold;

     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: center;
  }
  
  
  .Nav-Item:hover {
    background-color: rgb(208, 147, 248);
  }
  
  .Nav-Item:active {
    color: orange;
  }


  .Nav-Item-Link {
    width: 100%;
 /*      align-items: center;
       justify-content: center;*/

    /*   transition: filter 300ms;*/
  }
/*
  .Nav-Item-Link:hover {
    filter: brightness(1.2);
  }*/


  .Nav-Dropdown {
    background-color: rgb(171, 61, 245);
    position: absolute;
    top: calc(var(--nav-size) - 5px);

    width: calc(var(--nav-size) * 2.0);
  /* transform: translateX(-100%);*/
   /* overflow: hidden;*/
    border: var(--border);
    border-radius: var(--border-radius);
    padding: 1rem;

    display: flex;
    flex-direction: column;

    justify-items: flex-start;
    align-items: flex-start;
  }

  .Nav-DropDownItem {
   /* display: block;*/

  /*  text-align: center;*/
    padding: 6px 3px;
    font-size: 14px;
 /*   text-decoration: none;*/
  }

  .Nav-DropDownItem:hover {
    color: rgb(150, 142, 221);
   }